<template>
  <header>
    <nav class="container">
      <div class="branding">
        <router-link class="header" :to="{ name: 'Home' }"
          >FireBlogs</router-link
        >
      </div>
      <div class="nav-links">
        <ul v-show="!mobile">
          <router-link class="link" :to="{ name: 'Home' }">首页</router-link>
          <router-link class="link" :to="{ name: 'Blogs' }">博客</router-link>
          <router-link v-if="admin" class="link" :to="{ name: 'CreatePost' }">发文章</router-link>
          <router-link v-if="!user" class="link" :to="{ name: 'Login' }"
            >登录/注册</router-link
          >
        </ul>
        <div v-if="user" @click="toggleProfileMenu" class="profile" ref="profile">
          <span>{{ this.$store.state.profileName }}</span>
          <div v-show="profileMenu" class="profile-menu">
            <div class="info">
              <p class="name">{{ this.$store.state.profileName }}</p>
              <div class="right">
                <p>{{ this.$store.state.profileUsername }}</p>
                <p>{{ this.$store.state.profileEmail }}</p>
              </div>
            </div>
            <div class="options">
              <div class="option">
                <router-link class="option" :to="{name: 'Profile'}">
                  <userIcon class="icon" />
                  <p>账号设置</p>
                </router-link>
              </div>
              <div v-if="admin" class="option">
                <router-link class="option" :to="{name: 'Admin'}">
                  <adminIcon class="icon" />
                  <p>管理员主页</p>
                </router-link>
              </div>
              <div @click="signOut" class="option">
                  <signOutIcon class="icon" />
                  <p>退出登录</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </nav>
    <menuIcon @click="toggleMobileNav" class="menu-icon" v-show="mobile" />
    <transition name="mobile-nav">
      <ul class="mobile-nav" v-show="mobileNav">
        <router-link class="link" :to="{ name: 'Home' }">首页</router-link>
        <router-link class="link" :to="{ name: 'Blogs' }">博客</router-link>
        <router-link v-if="admin" class="link" :to="{ name: 'CreatePost' }">发文章</router-link>
        <router-link v-if="!user" class="link" :to="{ name: 'Login' }"
          >登录/注册</router-link
        >
      </ul>
    </transition>
  </header>
</template>

<script>
import menuIcon from "../assets/Icons/bars-regular.svg";
import userIcon from "../assets/Icons/user-alt-light.svg";
import adminIcon from "../assets/Icons/user-crown-light.svg";
import signOutIcon from "../assets/Icons/sign-out-alt-regular.svg";
import firebase from "firebase/app";
import "firebase/auth";

export default {
  name: "navigation",
  components: {
    menuIcon,
    userIcon,
    adminIcon,
    signOutIcon,
  },
  data() {
    return {
      profileMenu: null,
      mobile: null,
      mobileNav: null,
      windowWidth: null,
    };
  },
  created() {
    window.addEventListener("resize", this.checkScreen);
    this.checkScreen();
  },
  methods: {
    /* 判断屏幕大小，是否要出现侧边栏 */
    checkScreen() {
      this.windowWidth = window.innerWidth;
      if (this.windowWidth <= 750) {
        this.mobile = true;
        return;
      }
      this.mobile = false;
      this.mobileNav = false;
      return;
    },
    /* 三小杠是否切换 */
    toggleMobileNav() {
      this.mobileNav = !this.mobileNav;
    },
    toggleProfileMenu(e) {
      if (e.target == this.$refs.profile) {
        this.profileMenu = !this.profileMenu;
      }
    },

    signOut() {
      firebase.auth().signOut();
      window.location.reload();
    },
  },
  computed: {
    user() {
      return this.$store.state.user;
    },
    admin() {
      return this.$store.state.isAdmin;
    }
  },
};
</script>

<style lang="scss" scoped>
header {
  background-color: #fff;
  padding: 0 25px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
  z-index: 99;

  .link {
    font-weight: 500;
    padding: 0 8px;
    transition: 0.3s color ease;

    &:hover {
      color: #1eb8b8;
    }
  }

  nav {
    display: flex;
    padding: 25px 0;

    .branding {
      display: flex;
      align-items: center;

      .header {
        font-weight: 600;
        font-size: 24px;
        color: #000;
        text-decoration: none;
      }
    }

    .nav-links {
      position: relative;
      display: flex;
      flex: 1;
      align-items: center;
      justify-content: flex-end;
    }

    ul {
      margin-right: 32px;

      .link {
        margin-right: 32px;
      }

      .link:last-child {
        margin-right: 0;
      }
    }

    .profile {
      position: relative;
      cursor: pointer;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 10px;
      width: 50px;
      height: 50px;
      border-radius: 50%;
      color: #fff;
      background-color: #303030;
      span {
        pointer-events: none;
      }
    }
    .profile-menu {
      position: absolute;
      top: 60px;
      z-index: 888;
      right: 0;
      width: 250px;
      background-color: #303030;
      box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1),
        0 2px 4px -1px rgba(0, 0, 0, 0.06);
      .info {
        display: flex;
        align-items: center;
        padding: 15px;
        border-bottom: 1px solid #fff;
        .name {
          position: initial;
          width: 40px;
          height: 40px;
          background-color: #fff;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 50%;
          color: #303030;
        }
        .right {
          flex: 1;
          margin-left: 24px;
          p:nth-child(1) {
            font-size: 14px;
          }
        }
      }
      .options {
        padding: 15px;
        .option {
          text-decoration: none;
          color: white;
          display: flex;
          align-items: center;
          margin-bottom: 12px;
          .icon {
            width: 18px;
            height: 18px;
            fill: white;
          }
          p {
            font-size: 14px;
            margin-left: 12px;
          }
        }
        .option:last-child {
          margin-bottom: 0;
        }
      }
    }
  }

  .menu-icon {
    cursor: pointer;
    position: absolute;
    top: 32px;
    right: 25px;
    height: 25px;
    width: 25px;
  }

  .mobile-nav {
    padding: 20px;
    width: 70%;
    max-width: 250px;
    display: flex;
    flex-direction: column;
    position: fixed;
    height: 100%;
    background-color: #303030;
    top: 0;
    left: 0;

    .link {
      padding: 15px 0;
      color: #fff;
    }
  }

  .mobile-nav-enter-active,
  .mobile-nav-leave-active {
    transition: all 1s ease;
  }

  .mobile-nav-enter {
    transform: translateX(-250px);
  }

  .mobile-nav-enter-to {
    transform: translateX(0);
  }

  .mobile-nav-leave-to {
    transform: translateX(-250px);
  }
}
</style>